header {
    margin: 0;
    height: 100vh;
}

.header__container {
    height: 100%;
    width: 100%;
    padding-top: 150px;
    text-align: center;
    position: relative;
    top: 20%;
}

.cta {
    margin-top: 10rem;
    padding: 5px;
    display: block;
    grid-gap: 1 rem;
    gap: 1 rem;
    justify-content:center;
}


.header__socials {
    display: flex;
    flex-direction: column;
    font-size: 1.5rem;
    align-items: center;
    grid-gap: 1rem;
    gap: 1rem;
    position: absolute;
    top: 40%;
    left: 10%;
}

@media only screen and (max-width: 600px) and (min-width: 1px) {
    header {
        height: 100vh;
    }

    .header__socials {
        display: none;
    }
}
nav {
    background: rgba(0, 0, 0, 0.3);
    width: -webkit-max-content;
    width: max-content;
    display: block;
    padding: 0.7rem, 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    bottom: 2rem;
    display: flex;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    border-radius: 3rem;
    overscroll-behavior-block: blur(15px);
}

nav a {
    background: transparent;
    padding: 0.9rem;
    border-radius: 50%;
    display: flex;
    color: var(--color-light);
    font-size: 1.1rem;
}
nav a:hover {
    background: rgba(0, 0, 0, 0.5);
}

nav a.active {
    background: var(--color-primary);
    color: var(--color-bg);
}


h2 {
    text-align: center;
    padding: 2rem;
    font-size: xx-large;
}

h3 {
    padding: 2rem 0 0 0;
    font-size: larger;
}

.experience__container {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    grid-gap: 35px;
    gap: 35px;
}

.experience__container > div {
    background: var(--color-bg-variant);
    padding: 3rem 5rem;
    border-radius: 2rem;
    transition: var(--transition);
}   

.experience__container > div:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
}

.experience__container > div h3 {
    text-align: center;
    margin-bottom: 2rem;
    color: var(--color-white);
}

.experience__details {
    display: flex;
    padding-left: 20%;
    grid-gap: 0.5rem;
    gap: 0.5rem;
}

.experience__details-icon {
    margin-top: 2px;
    color: var(--color-primary);
}

@media only screen and (max-width: 600px) {
    .experience__container {
        margin: 20px;
    }
}
h2 {
   text-align: center;
   padding-top: 2rem;
   font-size: xx-large;
}

h3 {
    padding: 2rem 0 0 0;
    font-size: larger;
}

.portfolio__container {
    display: grid;
    grid-template-columns: auto auto auto;
    padding: 5rem;
    grid-gap: 35px;
    gap: 35px;
    justify-content: center;
}

.portfolio__item {
    background: var(--color-bg-variant);
    padding: 1rem;
    border-radius: 2rem;
    text-align: center;
    transition: var(--transition);
}

.portfolio__item:hover {
    border-color: var(--color-primary-variant);
    background: transparent;
}

@media only screen and (max-width: 600px) {
.portfolio__container {
    grid-template-columns: auto;
    padding: 2rem;
}
}

h2 {
    text-align: center;
    padding-top: 2rem;
    font-size: xx-large;
}

h3 {
    padding: 2rem 0 0 0;
    font-size: larger;
    text-align: center;
}

.contact__container {
    display: grid;
    grid-template-columns: auto auto auto;
    padding: 5rem;
    grid-gap: 35px;
    gap: 35px;
    justify-content: center;
}

.contact_option {
    background: var(--color-bg-variant);
    padding: 5rem;
    border-radius: 2rem;
    text-align: center;
    transition: var(--transition);

}

.contact__option:hover {
    background: transparent;
    border-color: var(--color-primary-variant);
}

.contact__option-icon {
    font-size: 3rem;
}

a {
    color: white;
}

.gmail {
    color: #EA4335;
}

.fb {
    color:#06276a;
}

.whatsapp {
    color: #25D366;
}

@media only screen and (max-width: 600px) {
    .contact__container {
        grid-template-columns: auto;
    }
}


* {
    margin: 0;
    padding: 0;
    border: 0;
}

:root {
    --color-bg: #0A1612;
    --color-bg-variant: #2e598a;
    --color-primary: #4db5ff;
    --color-primary-variant: #fff;
    --color-white: #fff;
    --color-light: rgba(255, 255, 255, 0.6);

    --transition: all 500ms ease; 
}

html {
    scroll-behavior: smooth;
}

::-webkit-scrollbar {
    display: none;
}

body {
font-family: 'Nunito', sans-serif;
color: #fff;
color: var(--color-white);
background: #000 url(/swarup_website/static/media/stars.6494ab5b.png) repeat top center;
z-index: 0;
}

@-webkit-keyframes move-twink-back {
    from {
        background-position: 0 0;
    }

    to {
        background-position: -1000px 2500px;
    }
}

@keyframes move-twink-back {
    from {
        background-position: 0 0;
    }

    to {
        background-position: -1000px 2500px;
    }
}

@-webkit-keyframes move-clouds-back {
    from {
        background-position: 0 0;
    }

    to {
        background-position: 0 10000px;
    }
}

@keyframes move-clouds-back {
    from {
        background-position: 0 0;
    }

    to {
        background-position: 0 10000px;
    }
}

/* .twinkling {
    background: transparent url(./images/twinkling.png) repeat center;
    z-index: 1;
    animation: move-twink-back 150s linear infinite;
} */

.clouds {
    background: transparent url(/swarup_website/static/media/clouds.98616c6b.png) repeat top center;
    z-index: 2;
    animation: move-clouds-back 400s linear alternate-reverse;
}


/*======GENERAL STYLES=======*/

h1, h2, h3, h4, h5 {
    font-weight: 500;
}

.btn {
    margin: 17px;
    width: -webkit-max-content;
    width: max-content;
    display: inline-block;
    text-align: center;
    color: #4db5ff;
    color: var(--color-primary);
    padding: 0.75rem 1.2rem;
    border-radius: 0.4rem;
    cursor: pointer;
    border: 1px solid #4db5ff;
    border: 1px solid var(--color-primary);
    transition: all 500ms ease;
    transition: var(--transition);
}

.btn:hover {
    background: #fff;
    background: var(--color-white);
    color: #0A1612;
    color: var(--color-bg);
    border-color: transparent;
}

.btn-primary {
    background: #4db5ff;
    background: var(--color-primary);
    color: #0A1612;
    color: var(--color-bg);
}

img {
    display: block;
    width: 100%;
    object-fit: cover;
    border-radius: 2rem;
}

